.body{
    /* overflow-x: hidden; */
    margin: 0%;
    padding: 0%;
}
.contNav{
    background-color: #333333;
    display: flex;
    justify-content: center;
    height: 50px;
}
.wrapNav{
    width: 95%;
    display: flex;
    justify-content: center;
}
.contNav .logo{
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    font-size: 19px;   
}
.contNav .logo li{
    list-style: none;
    color: #3cdfff;
}
.contNav .nav{
    margin-left: 630px;
}
.nav ul{
    display: flex;
}
.contNav .nav li{
    list-style: none;
    margin-right: 50px;

}
.contNav .nav li:hover{
    color: #3cdfff;
    transition: ease-in-out 0.4s;
    cursor: pointer;
}