.body{
    margin: 0%;
    padding: 0%;
   
}
.Container{
    background-color: #252323;
    width: 100%;
    height: 100%;
   
}
.wrapHome{
    justify-content: center;
    display: flex;
}
.Container .home{
    display: flex;
    justify-content: center;
    width: 85%;
    height: 350px;
}
.wrap{
    margin-top: 50px;
    width: 350px;
    height: 400px;
    margin-right: 420px;
}
.wrap .profile{
    width: 400px;
}
.wrap .profile h2{
    font-size: 33px;
    color: #3cdfff;
}
.wrap .profile .front{
    font-size: 23px;
}
.wrap .profile .front span{
    color: #3cdfff;
}
.wrap .profile .frontEnd{
    font-size: 14px;
    color: #fff;
}
.wrap .profile button{
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px;
    width: 270px;
    height: 30px;
    margin-left: 20px;
    background-color: #3cdfff;
    border: none;
    border-radius: 5px;
    font-size: 17px;
}
.wrap .profile button:hover{
    background-color: black;
    color: #3cdfff;
    transition: ease-in-out 0.4s;
}
.wrap2{
    margin-top: 50px;
    width: 300px;
    height: 400px;
}
.wrap2 .foto{
    margin-top: 40px;
}
.wrap2 .foto img{
    border-radius: 30px;
    width: 230px;
}

.wrapAbout{
    justify-content: center;
    display: flex;
    margin-top: 80px;
    height: 350px;
    background-color: #333333;
    width: 80%;
    margin-left: 150px;
    border-radius: 14px;
}
.Container .about{
    display: flex;
    justify-content: center;
    width: 85%;
    height: 300px;
    margin-bottom: 200px;
}
.wrap3{
    margin-top: 50px;
    width: 80%;
    height: 400px;
}
.wrap3 h2{
    text-align: center;
    font-size: 25px;
    margin-top: -50px;
    color: #3cdfff;
}
.Container .about .aboutMe{
    display: flex;
}
.Container .about .aboutMe p{
    color: white;
}
.Container .about .aboutMe .foto img{
    width: 200px;
    border-radius: 30px;
    margin-right: 30px;
}
.wrapProjek{
    justify-content: center;
    display: flex;
    height: 380px;
}
.Container  .txt{
    color: #3cdfff;
    position: absolute;
    height: 30px;
    display: flex;
    justify-content: center;
}
.Container .wrap5{
    height: 400px;
    display: flex;
    justify-content:center;
    margin-left: 30px;
    margin-top: 70px;    
}
.Container .wrap5 .box{
    background-color: gray;                                                         
    width: 300px;
    height: 250px;
    border-radius: 15px;
}
.Container .wrap5 .isiBox{
    justify-content: center;
}
.Container .wrap5 .isiBox h5{
    text-align: center;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    font-size: 14px;
}
.Container .wrap5 .box .isiBox p{
    color: white;
}
.Container .wrap5 .isiBox img{
    margin-left: 25px;
    justify-content: center;
    width: 270px;
    margin-right: 10px;
}
.Container .wrap5 .isiBox p{
    text-align: center;
    font-size: 13px;
}
 .skil{
    justify-content: center;
    display: flex;
    height: 390px;
}
.skil .skill{
    background-color: #333333;
    justify-content: center;
    display: flex;
    width: 80%;
    height: 370px;
    border-radius: 19px;
}
.skil .skill .wrapSkill{
    width: 90%;
    text-align: center;
}
.skil .skill .wrapSkill h2{
    color: #3cdfff;
}
.wrap6{
    margin-left: 50px;
    width: 90%;
    height: 265px;
    justify-content: center ;
    display: flex;

}
.boxSkill{
    margin-top: 35px;
    margin-right: 10px;
    margin-left: 10px;
    width: 250px;
    background-color: #fff;
    height: 200px;
    display: flex;
    justify-content: center;
    border-radius: 8px;
    transition: ease-in-out 0.5s;
}
.boxSkill .mySkill{
    justify-content: center;
}
.boxSkill2{
    /* background-color: red; */
    margin-top: 35px;
    margin-right: 10px;
    margin-left: 10px;
    width: 250px;
    background-color: black;
    height: 200px;
    display: flex;
    justify-content: center;
    border-radius: 8px;
    transition: ease-in-out 0.5s;
}
.boxSkill2 .mySkill2{
    justify-content: center;
    background-color: transparent;
 
}

.boxSkill .mySkill .logo{
    height: 130px;
    color: #e34c26;
}
.boxSkill .mySkill .logo2{
    height: 130px;
    color: #2965f1;
}
.boxSkill2 .mySkill2 .logo3{
    /* background-color: green; */
    height: 130px;
    color: #f0db4f;
}
.boxSkill2 .mySkill2 .logo4{
    /* background-color: green; */
    height: 130px;
    color: #3cdfff;
}
.boxSkill2 .mySkill2 p{
    font-size: 18px;
    color: white    ;
}
.boxSkill:hover{
    background-color: black;
    color: white ;
    transform: scale(1.1);
}
.boxSkill2:hover{
    background-color: white;
    transform: scale(1.1);
}
.boxSkill2:hover p{
    color: black;

}
.contact{
